export default {
  name: "theav",
  data() {
    return {
      topic_data: undefined,
      download_data: undefined
    };
  },
  created() {
    this.id = this.$route.params.id;
    console.log('this.id:', this.id);
    this.topic_data = this.get_topic_data_from_local(this.id);
    this.load_download_data();
  },
  methods: {
    load_download_data: function () {
      this.$axios.get('/app/download/torrent', {
        params: {
          id: this.id,
        }
      }).then((response) => {
        var download_data = response.data;
        console.log("download_data:", download_data);
        this.download_data = download_data;
        this.loading = false;
        this.set_title(  this.topic_data.title + '-- 种子磁力下载');
      }).catch((error) => {
        console.log("error:", error);
        this.loading = false;
      });
    }
  }
}
